<template>
	<div class="gradient" :style="{'background-image': gradient.code}" @click.prevent="onClicked">
	</div>
</template>
<script>
export default{
  name: 'gradient',
  props: ['gradient'],
  methods: {
    onClicked () {
      window.data = JSON.stringify({gradient: this.gradient})
      this.$router.push(`/${Date.now()}`)
    }
  }
}
</script>
<style lang="scss" scoped>
	.gradient{
		cursor: pointer;
		height: 70px;
		width: 70px;
		margin: 10px;
		overflow: hidden;
		box-shadow: 0 5px 5px 0 #dfe6f4;
		border-radius: 5px;
		transition: all .2s ease-in-out;
		position: relative;
	}
</style>



